.faq-conatainer {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  gap: 2rem;
  padding: 5px;
}

.faq-title-icons {
  display: flex;
  gap: 1rem;
}

.faq-title-icons > p {
  font-size: 2rem;
}
.faq-conatainer > ul > li {
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.faq-accordion {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.faq-accordion > h2 {
  border-bottom: 2px solid white;
}

.faq-accordion > p {
  width: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.2rem;
  cursor: pointer;
}

.faq-conatainer > ul > li > p {
  text-align: justify;
  word-spacing: 1px;
  font-weight: 400;

  font-family: sans-serif;
  overflow: hidden;
  height: 0;
}
